
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
data() {
return {
checkStyle: {padding: '5px 10px 10px',textAlign:'left',display: 'flex',justifyContent: 'left',alignItems: 'left'},
selectedTabs: [],
tabDatas: [],
demo6:[],
selected:[]

}
},
components: {
Checker,CheckerItem,Divider,XButton
},
props: ['tags'],
created(){
var _this=this;
this.util.loadDic({dicName:'',type:1},function(data){

_this.tabDatas = data;





});
},
methods: {
onItemClick (value) {
this.selectedTabs=[];
this.selected = [];
for(var i=0;i<this.demo6.length;i++){
this.selected.push(i);
this.selectedTabs.push(this.demo6[i]);
}


},
close(){
this.tags.tagsVisible=false;
},
saveProTags(){
var tags = this.selectedTabs.join(",");
this.tags.tagsVisible=false;
this.$emit('chooseTags',{proTags: tags});
}
}

}



